import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
// import { resetPassword } from '../api';

const ForgotPassword = () => {
  const [email, setEmail] = useState('');
  const navigate = useNavigate();

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!email) {
      alert('Please enter your email');
      return;
    }

    // Simulate API call
    console.log('Reset email:', email);
    alert('Reset link sent to your email');
    navigate('/');
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-r from-green-500 to-emerald-600 p-4">
      <div className="bg-white p-8 rounded-xl shadow-lg w-full max-w-md">
        <h2 className="text-2xl font-bold text-center text-emerald-700 mb-6">Forgot Password</h2>
        <form onSubmit={handleSubmit}>
          <div className="mb-4">
            <label className="block text-gray-700 font-semibold mb-1">Email</label>
            <input
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className="w-full border border-gray-300 rounded p-2"
            />
          </div>

          <button type="submit" className="w-full bg-emerald-600 hover:bg-emerald-700 text-white font-semibold py-2 px-4 rounded">
            Send Reset Link
          </button>

          <div className="mt-4 text-center">
            <button onClick={() => navigate('/')} className="text-emerald-600 hover:underline text-sm">Back to Login</button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default ForgotPassword;
